// В этом файле должны быть стили для БЭМ-блока navbar, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.navbar {

  $block-name:                &; // #{$block-name}__element

  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 120rem;
  height: 100%;

  &__wrapper {
    display: flex;
    justify-content: center;
    width: 100%;
    height: 8rem;
    border-bottom: 0.1rem solid #aaa;
  }

  &__logo {
    position: relative;

    font-size: 3.5rem;
    font-style: italic;
    font-weight: 800;
    color: #fff;
  }

  &__logo-p1 {
    position: relative;
    z-index: 10;
    margin-right: 1rem;

    font-size: 4.5rem;
  }

  &__logo-p2 {
    position: absolute;
    top: 0.4rem;
    left: 0.5rem;

    font-size: 4.5rem;
    color: #bbb;
  }
}
